.idm-columns-layout{
    box-sizing: border-box;
    overflow: hidden;
    display: flex;
    @for $i from 1 through 24 {
        &>.flex-#{$i}{
            flex:#{$i} #{$i} auto;-webkit-box-flex:#{$i};-webkit-flex:#{$i};
        }
    }
}
.idm-inlineblock-container-layout{
    display: flex;
    flex-direction:row;
    flex-wrap: wrap;
}
.idm-body-scroll-disabled{
    overflow: hidden !important;
}